import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"

import Img from "gatsby-image"
import GlobalStateProvider from "../context/provider"
import Layout from "../components/layout"
import SEO from "../components/seo"
import ContentWrapper from "../styles/contentWrapper"

const StyledSection = styled.section`
  width: 100%;
  max-width: 62.5rem;
  margin: 0 auto;
  padding: 0 2.5rem;
  height: auto;
  background: ${({ theme }) => theme.colors.background};
  h1 {
    font-size: 1.5rem;
  }
  p {
    font-style: italic;
    margin-left: 0.5rem;
    font-size: 0.85rem;
  }
  .screenshot {
    box-shadow: 0 0 2.5rem rgba(0, 0, 0, 0.16);
    transition: all 0.3s ease-out;
    border-radius: ${({ theme }) => theme.borderRadius};
    &:hover {
      transform: translate3d(0px, -0.125rem, 0px);
      box-shadow: 0 0 2.5rem rgba(0, 0, 0, 0.32);
    }
  }
`

const StyledContentWrapper = styled(ContentWrapper)`
  && {
    width: 100%;
    max-width: 36rem;
    margin: 0;
    padding: 0;
    height: 100%;
  }
`

const DailyNewsHighlights = ({ data }) => {
  const globalState = {
    isIntroDone: false,
    darkMode: false,
  }

  const { dailyNewsHighlights } = data
  const { frontmatter } = dailyNewsHighlights.edges[0].node

  return (
    <GlobalStateProvider initialState={globalState}>
      <Layout>
        <SEO
          title="Peter H. | Daily News Highlights"
          meta={[{ name: "robots", content: "noindex" }]}
        />
        <StyledSection>
          <StyledContentWrapper>
            <h1 data-testid="heading">Daily News Highlights</h1>
            <Img
              className="screenshot"
              fluid={frontmatter.image.childImageSharp.fluid}
            />
            <p>Screenshot of email sample</p>
          </StyledContentWrapper>
        </StyledSection>
      </Layout>
    </GlobalStateProvider>
  )
}

DailyNewsHighlights.propTypes = {
  data: PropTypes.object.isRequired,
}

export default DailyNewsHighlights

export const pageQuery = graphql`
  {
    dailyNewsHighlights: allMdx(
      filter: { fileAbsolutePath: { regex: "/index/dailyNewsHighlights/" } }
    ) {
      edges {
        node {
          frontmatter {
            image {
              childImageSharp {
                fluid(maxWidth: 400, quality: 90) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`
